<template lang="pug">
  #eassyCont.wrapper
    navbar
    main
      button.btn_backToTop.mb_hide(@click="backToTop")
      mainIneer
      mentaRecommend
</template>

<script>
import $ from 'jquery';
import navbar from '../components/navbar.vue';
import mainIneer from '../components/eassyCont/mainInner.vue';
import mentaRecommend from '../components/eassyCont/mentaRecommend.vue';

export default {
  components: {
    navbar,
    mainIneer,
    mentaRecommend,
  },
  methods: {
    backToTop() {
      $('html, body').animate({ scrollTop: 0 }, 200);
    },
  },
  mounted() {
    const $win = $(window);
    const $backToTop = $('.btn_backToTop');
    $win.scroll(() => {
      if ($win.scrollTop() > 200) {
        $backToTop.show();
      } else {
        $backToTop.hide();
      }
    });
  },
};
</script>

